

import { Direction, Directionality } from '@angular/cdk/bidi';
import { BooleanInput } from '@angular/cdk/coercion';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  Renderer2,
  SimpleChanges,
  ViewEncapsulation
} from '@angular/core';
import { Subject } from 'rxjs';
import { ButtonSize } from "tds-ui/core/config";
import { NgStyleInterface } from 'tds-ui/core/config';
import { InputBoolean } from 'tds-ui/shared/utility';
import { TDSTagStatusType } from './types';

@Component({
  selector: 'tds-tag',
  exportAs: 'tdsTag',
  templateUrl: './tag.component.html',
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  host: {
    class: "group inline-flex box-content",
    '(click)': 'updateCheckedStatus()'
  }
})
export class TDSTagComponent implements OnChanges, OnDestroy, OnInit {
  @Input() mode: 'default' | 'closeable' | 'checkable' = 'default';
  @Input() type: 'default' | 'outline' = 'default';
  @Input() status: TDSTagStatusType | string = 'secondary';
  @Input() tdsStyle?: NgStyleInterface;
  @Input() @InputBoolean() checked = false;
  @Input() size: ButtonSize = "md";
  @Input() @InputBoolean() disabled: boolean = false;
  @Input() rounded: string | null = null;
 

  @Output() readonly close = new EventEmitter<MouseEvent>();
  @Output() readonly checkedChange = new EventEmitter<boolean>();
  cssClass: string = '';
  private destroy$ = new Subject<void>();
  constructor(private _cdr: ChangeDetectorRef,
    private renderer: Renderer2,
    private elementRef: ElementRef) {
  }
  ngOnInit() {
    this.buildCss();
  }
  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }
  ngOnChanges(changes: SimpleChanges): void {
    // const { size, disabled, rounded } = changes;
    // if (size || disabled || rounded) {
    this.buildCss();
    this._cdr.markForCheck();
    // }
  }
  onClickClose(e: MouseEvent) {
    if (this.mode === 'closeable' && !this.disabled) {
      this.close.emit(e);
      if (!e.defaultPrevented) {
        this.renderer.removeChild(this.renderer.parentNode(this.elementRef.nativeElement), this.elementRef.nativeElement);
      }
    }
  }
  updateCheckedStatus(): void {
    if (this.mode === 'checkable' && !this.disabled) {
      this.checked = !this.checked;
      this.checkedChange.emit(this.checked);
    }
  }
  buildCss() {
    if (this.mode == 'default') {
      if (this.type == 'default') {
        this.cssClass = this.cssDefault();
      } else {
        this.cssClass = this.cssOutline();
      }
    }
  }
  get getRounded() {
    if (this.rounded) {
      return this.rounded
    } else {
      if (this.mode === 'checkable') {
        return 'rounded-md'
      } else {
        return 'rounded'
      }
    }
  }
  private cssDefault() {
    let cls =
      'px-2 py-0.5  text-caption-2 font-semibold text-white ';
    let clsBackground = this.convertClass();
    return ` ${cls}  ${this.getRounded} ${clsBackground}`;
  }
  private cssOutline() {
    let cls =
      'px-2 py-0.5  text-caption-2 font-semibold  border';
    let clsBackground = this.convertOutlineClass();
    return ` ${cls}  ${this.getRounded} ${clsBackground}`;
  }
  private convertClass() {
    let result = null;
    switch (this.status) {
      case 'primary':
        result = 'bg-primary-1 dark:bg-d-primary-1';
        break;
      case 'secondary':
        result = 'bg-neutral-1-400 dark:bg-d-neutral-1-300';
        break;
      case 'success':
        result = 'bg-success-400 dark:bg-d-success-400';
        break;
      case 'info':
        result = 'bg-info-400 dark:bg-d-info-400';
        break;
      case 'warning':
        result = 'bg-warning-400 dark:bg-d-warning-400';
        break;
      case 'error':
        result = 'bg-error-400 dark:bg-d-error-400';
        break;
      default:
        result = this.status;
        break;
    }
    return result;
  }
  private convertOutlineClass() {
    let result = null;
    switch (this.status) {
      case 'primary':
        result = ' bg-primary-3 text-primary-1 border-primary-1 dark:bg-d-primary-3 dark:text-d-primary-1 dark:border-d-primary-1';
        break;
      case 'secondary':
        result = ' bg-neutral-3-100 border-neutral-1-300 border text-neutral-1-700 dark:bg-d-neutral-3-100 dark:border-d-neutral-1-300 dark:text-d-neutral-1-700';
        break;
      case 'success':
        result = ' bg-success-100 border-success-300  text-success-400 dark:bg-d-success-100 dark:border-d-success-300  dark:text-d-success-400';
        break;
      case 'info':
        result = ' bg-info-100 border-info-300  text-info-400 dark:bg-d-info-100 dark:border-d-info-300  dark:text-d-info-400';
        break;
      case 'warning':
        result = ' bg-warning-100 border-warning-300  text-warning-400 dark:bg-d-warning-100 dark:border-d-warning-300  dark:text-d-warning-400';
        break;
      case 'error':
        result = 'bg-error-100 border-error-300  text-error-400 dark:bg-d-error-100 dark:border-d-error-300  dark:text-d-error-400';
        break;
      default:
        result = this.status;
        break;
    }
    return result;
  }
}
